.header-option {
  display: flex;
  flex-direction: column;
  color: gray;
  align-items: center;
  font-size: 8px;
  cursor: pointer;
  margin-right: 20px;
  transition: all 0.5s;
}
.header-option:hover {
  color: black
}
.header-option > h3 {
  text-align: center;
  font-size: 12px;
}
.header-option_icon {
  object-fit:  contain !important; 
  height: 30px !important ;
  width: 30px !important ;
}