.post {
    background-color: #fff;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
}
.post__header {
    display: flex;
    margin-bottom: 10px;
}
.post__info {
    margin-left: 10px;
}
.post__info > p {
    font-size: 12px;
    color: gray;
}
.post__btns {
    display: flex;
    justify-content: space-evenly;
}